<template>
  <validation-provider
    :rules="rules"
    :name="name"
    v-bind="$attrs"
    v-slot="{ errors, valid, invalid, validated }"
  >
    <div class="form-group">
      <slot name="label">
        <label v-if="label" :class="labelClasses">
          {{ label }}
        </label>
      </slot>
      <div
        :class="[
          { 'input-group': hasIcon },
          { focused: focused },
          { 'input-group-alternative': alternative },
          { 'has-label': label || $slots.label },
          inputGroupClasses,
        ]"
        class="tk-position-relative"
      >
        <div v-if="prependIcon || $slots.prepend" class="input-group-prepend">
          <span class="input-group-text">
            <slot name="prepend">
              <i :class="prependIcon"></i>
            </slot>
          </span>
        </div>
        <slot v-bind="slotData">
          <input
            :value="value"
            :type="type"
            v-on="listeners"
            v-bind="$attrs"
            :valid="valid"
            :required="required"
            class="form-control"
            :class="[
              { 'is-valid': valid && validated && successMessage },
              { 'is-invalid': invalid && validated },
              inputClasses,
              {'tk-border-color-red': showResultStatus && !success}
            ]"
            v-model="realTimeValue"
          />
        </slot>
        <div v-if="appendIcon || $slots.append" class="input-group-append">
          <span class="input-group-text">
            <slot name="append">
              <i :class="appendIcon"></i>
            </slot>
          </span>
        </div>
        <div
          v-if="loading || showResultStatus"
          class="tk-position-absolute tk-bg-color-white"
          style="
            right: 3px;
            width: 40px;
            height: 40px;
            top: 50%;
            transform: translateY(-50%);
            text-align: center;
            line-height: 40px;
          "
        >
          <loading size="40" v-if="loading"></loading>
          <template v-if="showResultStatus">
            <i class="fas fa-check tk-font-color-main" v-if="success"></i>
            <!-- <i class="fas fa-times tk-font-color-red" v-else></i> -->
          </template>
        </div>
        <slot name="infoBlock"></slot>
      </div>
      <slot name="success">
        <div class="valid-feedback" v-if="valid && validated">
          {{ successMessage }}
        </div>
      </slot>
      <slot name="error">
        <div v-if="errors[0]" class="invalid-feedback" style="display: block">
          {{ errors[0] }}
        </div>
        <div v-if="!success && showResultStatus" class="invalid-feedback" style="display: block">
          {{ error }}
        </div>
      </slot>
    </div>
  </validation-provider>
</template>
<script>
import loading from "../layout/Loading";
export default {
  components: {
    loading,
  },
  inheritAttrs: false,
  name: "loading-input",
  props: {
    required: {
      type: Boolean,
      description: "Whether input is required (adds an asterix *)",
    },
    group: {
      type: Boolean,
      description:
        "Whether input is an input group (manual override in special cases)",
    },
    alternative: {
      type: Boolean,
      description: "Whether input is of alternative layout",
    },
    label: {
      type: String,
      description: "Input label (text before input)",
    },
    error: {
      type: String,
      description: "Input error (below input)",
    },
    successMessage: {
      type: String,
      description: "Input success message",
      default: "",
    },
    labelClasses: {
      type: String,
      description: "Input label css classes",
      default: "form-control-label",
    },
    inputClasses: {
      type: String,
      description: "Input css classes",
    },
    inputGroupClasses: {
      type: String,
      description: "Input group css classes",
    },
    value: {
      type: [String, Number],
      description: "Input value",
    },
    type: {
      type: String,
      description: "Input type",
      default: "text",
    },
    appendIcon: {
      type: String,
      description: "Append icon (right)",
    },
    prependIcon: {
      type: String,
      description: "Prepend icon (left)",
    },
    rules: {
      type: [String, Array, Object],
      description: "Vee validate validation rules",
      default: "",
    },
    name: {
      type: String,
      description: "Input name (used for validation)",
      default: "",
    },
    loading: {
      type: Boolean,
      description: "Loading or not",
      default: false,
    },
    success: {
      type: Boolean,
      description: "Success or not",
      default: false,
    }
  },
  data() {
    return {
      focused: false,
      showResultStatus: false,
      realTimeValue: "",
    };
  },
  watch: {
    loading(newVal, oldVal) {
      this.showResultStatus = !newVal
      // if (!this.success) {
      //   setTimeout(() => {
      //     this.showResultStatus = false
      //   }, 1500)
      // }
    },
  },
  created() {
    this.realTimeValue = this.value;
  },
  computed: {
    listeners() {
      return {
        ...this.$listeners,
        input: this.updateValue,
        focus: this.onFocus,
        blur: this.onBlur,
      };
    },
    slotData() {
      return {
        focused: this.focused,
        error: this.error,
        ...this.listeners,
      };
    },
    hasIcon() {
      const { append, prepend } = this.$slots;
      return (
        append !== undefined ||
        prepend !== undefined ||
        this.appendIcon !== undefined ||
        this.prependIcon !== undefined ||
        this.group
      );
    },
  },
  methods: {
    updateValue(evt) {
      let value = evt.target.value;
      this.$emit("input", value);
      this.showResultStatus = false
    },
    onFocus(evt) {
      this.focused = true;
      this.$emit("focus", evt);
    },
    onBlur(evt) {
      this.focused = false;
      this.$emit("blur", evt, this.realTimeValue);
    },
  },
};
</script>
