<template>
  <div style="width: 600px; margin: 50px auto">
    <!-- loading input -->
    <loading-input
      @blur="blur"
      :value="value"
      :loading="loading1"
      :success="success1"
      :error="error"
    ></loading-input>

    <!-- loading switch -->
    <div class="form-group">
      <div>
        <div class="form-control">
          <span
            class="tk-layout-inline-block tk-font-color-black1"
            style="width: calc(100% - 56px)"
            >Switch</span
          >
          <loading-switch
            class="mr-1 tk-layout-inline-block"
            v-model="opened"
            @input="onSwitchChanged"
            type="main"
            :loading="loading2"
            :success="success2"
          >
          </loading-switch>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import loadingInput from "../components/Inputs/LoadingInput";
import loadingSwitch from "../components/Inputs/LoadingSwitch";
export default {
  data() {
    return {
      loading1: false,
      loading2: false,
      value: "default value",
      error: "Something wrong, try again",
      opened: false,
      success1: false,
      success2: false,
    };
  },
  components: {
    loadingInput,
    loadingSwitch,
  },
  methods: {
    blur(evt, value) {
      let self = this;
      if (value !== this.value && !this.$tools.isNull(value)) {
        self.loading1 = true;
        this.value = value;
        setTimeout(() => {
          self.success1 = false;
          self.loading1 = false;
        }, 2000);
      }
    },
    onSwitchChanged(value) {
      let self = this
      self.loading2 = true;
      self.opened = value;
      setTimeout(() => {
        self.success2 = false;
        self.loading2 = false;
      }, 2000);
      
    },
  },
};
</script>

