<template>
  <label class="custom-toggle" :class="switchClass">
    <input type="checkbox" v-model="model" />
    <span
      class="custom-toggle-slider rounded-circle"
      :data-label-off="offText"
      :data-label-on="onText"
    ></span>
    <template v-if="loading">
      <loading size="24" v-if="model" color="white" style="left: 78%"></loading>
      <loading size="24" v-else style="left: 24%"></loading>
    </template>
    <!-- <template v-if="showResultStatus">
      <i class="fas fa-check tk-font-color-main" v-if="success"></i>
      <i class="fas fa-times tk-font-color-red" v-else></i>
    </template> -->
  </label>
</template>
<script>
import loading from "../layout/Loading";
export default {
  components: {
    loading,
  },
  name: "loading-switch",
  props: {
    value: [Array, Boolean],
    type: String,
    onText: {
      type: String,
      default: "Yes",
    },
    offText: {
      type: String,
      default: "No",
    },
    loading: {
      type: Boolean,
      description: "Loading or not",
      default: false,
    },
    success: {
      type: Boolean,
      description: "Success or not",
      default: false,
    },
  },
  watch: {
    loading(newVal, oldVal) {
      this.showResultStatus = !newVal;
      if (!this.success) {
        setTimeout(() => {
          this.showResultStatus = false;
        }, 1500);
      }
    },
  },
  computed: {
    switchClass() {
      let baseClass = "custom-toggle-";
      if (this.type) {
        return baseClass + this.type;
      }
      return "";
    },
    model: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      },
    },
  },
  data() {
    return {
      showResultStatus: false,
    };
  },
  methods: {
    triggerToggle() {
      this.model = !this.model;
    },
  },
};
</script>
<style></style>
